@import url('https://cdn.jsdelivr.net/npm/bootstrap-icons@1.7.1/font/bootstrap-icons.css');
@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@300&display=swap');

$primary-color: #f1b2b5 !default;
$testimonial-image: 80px;

.always-svg-disp {
  height: 0;
  width: 0;
  display: inline;
  .home__blob {
    width: 0;
    margin: 0;
    padding: 0;
  }
}

* {
  font-family: 'Spartan', sans-serif;
  box-sizing: border-box;
}

body {
  margin-top: 92px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
}

// NAVBAR//
nav {
  background: white;
}
.navbar-toggler {
  border: none;
}

#navCollapse {
  .navbar-nav {
    width: 40%;
    .nav-link {
      color: black;
      font-weight: bold;
      font-size: 0.9rem;
    }
  }
}

// HOME SECTION//
#home {
  .small-disp {
    display: none;
  }

  .home__blob-img {
    height: 90%;
    width: 90%;
  }
  .home__blob {
    width: 80%;
  }

  .large-disp {
    .social-icons {
      font-size: 1.4rem;
    }
    .intro {
      p {
        line-height: 35px;
      }
    }
  }

  .contact-button {
    color: white;
    cursor: pointer;
    border-radius: 5px;
    font-size: 0.95rem;
    padding: 0.9rem;
    margin-bottom: 40px;

    .iconify {
      font-size: 1.2rem;
      vertical-align: -4.5px;
    }
  }

  .down {
    border: none;
    padding: 0;
    margin-top: 150px;
    display: flex;
    align-items: center;
    font-weight: bold;
    cursor: pointer;
    font-size: 0.9rem;
    .scrl-txt {
      color: black;
    }
  }
  .down:hover {
    text-decoration: none;
  }
  .bi-mouse {
    font-size: 25px;
  }
  .bi-chevron-down {
    font-size: 15px;
  }
}

// ABOUT//

#about {
  img {
    width: 63%;
    animation: bounce 1s infinite alternate;
  }
  .row {
    margin: 70px 0;
    p {
      line-height: 35px;
      font-size: 0.92rem;
    }
  }
}

// SKILLS//

#skills {
  .skill-icon {
    color: $primary-color;
    font-size: 25px;
  }
  .col-md-4 {
    p {
      margin: 5px;
    }
  }
  .col-md-8 {
    display: flex;
    align-content: center;
    justify-content: end;
    img {
      width: 68%;
      animation: bounce 1s infinite alternate;
    }
  }
  .top-content {
    display: flex;

    align-items: center;
    min-height: 300px;
    .carousel-inner {
      .carousel-item {
        img {
          height: 100px;
        }
      }
    }
  }

  // //CARAOUSEL////

  .col-12 {
    position: relative;
    min-height: 300px;
    display: flex;
    align-items: center;
    overflow: hidden;
    .slide-pic {
      position: absolute;
      left: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 25%;
      transition: transform 1s;
      img {
        height: 100px;
      }
    }
  }
}

// //PITCH////
#pitch {
  .col-12 {
    display: grid;
    grid-template-columns: repeat(20, 1fr);
    grid-template-rows: repeat(20, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;

    .pitch-img {
      grid-area: 1 / 1 / 9 / 11;
    }
    .pitch-content {
      background-color: #ebecf0;
      line-height: 33px;
      padding: 3rem;
      grid-area: 7 / 9 / 21 / 21;

      .contact-button {
        color: white;
        cursor: pointer;
        padding: 15px;
        .iconify {
          font-size: 1.2rem;
          vertical-align: -4.5px;
        }
      }
    }
  }
}

// COLLABORATIONS

#collaborations {
  .col-12 {
    position: relative;
    min-height: 700px;
    display: flex;
    align-items: center;
    overflow: hidden;
    .slide-colab {
      position: absolute;
      width: 100%;
      transition: transform 1s;
    }
  }
  img {
    width: 200px;
  }
}

//// TESTIMONIALS////

#testimonials {
  .col-12 {
    position: relative;
    min-height: 700px;
    display: flex;
    align-items: center;
    overflow: hidden;

    .t-slide-cont {
      position: absolute;
      width: 33.33%;
      left: 0;
      display: flex;
      justify-content: center;
      transition: transform 2s;
    }

    .t-slide {
      display: flex;
      flex-direction: column;
      height: 550px;
      border-radius: 20px;
      width: 95%;

      padding: 1rem;
      box-shadow: 1px 3px 12px -1px rgba(0, 0, 0, 0.25);
      .hdr-img {
        width: 100%;
        display: flex;
        justify-content: end;
        height: 80px;
        img {
          height: 80px;
        }
      }
      .slide-body {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        height: 80%;
      }
      .bi {
        color: #bbbbbb;
        font-size: 40px;
      }
      .testimonial__text {
        font-style: italic;
        font-size: 0.85rem;
        line-height: 25px;
      }

      .slide-footer {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .testimonial-image {
          width: $testimonial-image;
          height: $testimonial-image;
          border-radius: 50%;
        }
        div {
          display: flex;
          flex-direction: column;
          justify-content: end;
          p {
            font-size: small;
          }
        }
      }
    }
  }
  .c-steve {
    p {
      font-size: 0.7rem !important;
      line-height: 20px !important;
    }
  }
}

// // CONTACT////
#contact {
  .calendly-inline-widget {
    min-width: 270px;
    height: 650px;
  }
  .row {
    .btn {
      width: 50%;
      color: white;
    }
    .wA-cont {
      display: flex;
      justify-content: flex-end;
    }
  }
  .modal-content {
    border-top: 6px solid $primary-color;
    .modal-title {
      font-size: 1rem;
    }
    form {
      ::placeholder {
        font-size: 0.8rem;
      }
    }
  }
}

// //BLOGS////

#blog {
  a {
    text-decoration: none;
    .card {
      transition: transform 1s;
      .card-body {
        div {
          height: 50px;
        }
      }
    }
    h5 {
      font-size: 0.95rem;
      color: black;
      text-align: center;
    }
  }
  .card {
    img {
      filter: grayscale(1);
    }
  }
}
.shadow {
  box-shadow: -5px 2px 70px 0px rgba(0, 0, 0, 0.26);
  transform: scale(1.07);
}
.read-more {
  font-size: 0.85rem;
  font-weight: bold;
}

// //FOOTER////
footer {
  height: 120px;
  .bi {
    color: white;
    font-size: 1.4rem;
  }
  .text-center {
    font-size: small;
  }
}

//// ANIMATIONS////

@keyframes bounce {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-15px);
  }
}

@keyframes slide-bottom {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(3px);
  }
}
@keyframes slide-top {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-3px);
  }
}

//// MEDIA QUERY////

@media (max-width: 768px) {
  .mb-rspv {
    margin-bottom: 50px;
  }

  #navCollapse {
    .navbar-nav {
      width: 100%;
    }
  }
  #home {
    .small-disp {
      display: block;
      .social-icons {
        font-size: 1.2rem;
      }

      h1,
      h2 {
        font-size: 1.6rem;
      }
      .intro {
        font-size: 0.85rem !important;
        p {
          line-height: 30px;
        }
      }
      .contact-button {
        padding: 10px !important;
        margin-top: 0 !important;
        font-size: 12px;
      }
    }
    .large-disp {
      display: none;
    }
  }
  #about {
    h3 {
      font-size: 1.3rem;
    }
    h6 {
      font-size: 0.8rem;
    }
    img {
      animation: none;
      width: 95%;
    }
    h1 {
      font-size: 1.6rem;
    }
    h5 {
      font-size: 1rem;
    }
    .row {
      p {
        margin: auto;
        font-size: 0.9rem;
      }
    }
  }
  #skills {
    h4 {
      font-size: 1.1rem;
    }
    .col-md-4 {
      margin: auto;
      p {
        font-size: 0.8rem;
      }
    }
    .col-md-8 {
      img {
        width: 100%;
        animation: none;
        margin-top: 50px;
      }
    }
    .col-12 {
      .slide-pic {
        width: 100%;
      }
    }
  }
  #pitch {
    .col-12 {
      display: block;
      line-height: 20px;

      .pitch-img {
        margin-bottom: 25px;
      }
      .pitch-content {
        padding: 1.2rem;
        h4 {
          font-size: 1.3rem;
        }
        p {
          font-size: 0.9rem;
        }
        .contact-button {
          width: 100%;
          padding: 10px;
          font-size: 0.9rem;
        }
      }
    }
  }

  #collaborations {
    h4 {
      font-size: 1.2rem;
    }
    .col-12 {
      min-height: 1300px;
    }
    img {
      width: 150px;
    }
  }

  #testimonials {
    h6 {
      font-size: 0.8rem;
    }
    h3 {
      font-size: 1.02rem;
    }
    .lead {
      font-size: 1rem;
    }
    .t-slide-cont {
      width: 100% !important;
    }
    .t-slide {
      .hdr-img {
        height: 50px;
        img {
          height: 50px;
        }
      }
      .bi {
        font-size: 30px;
      }
      .testimonial__text {
        font-size: 0.75rem;
        line-height: 20px;
      }
      .slide-footer {
        .testimonial-image {
          width: 55px !important;
          height: 55px !important;
        }
        div {
          h6 {
            font-size: 0.8rem;
          }
          p {
            font-size: 9px;
          }
        }
      }
    }
    .c-steve {
      p {
        font-size: 0.55rem !important;
        line-height: 16px !important;
      }
    }
    .slide-resp {
      p {
        font-size: 0.65rem !important;
        line-height: 20px !important;
      }
    }
  }
  #contact {
    h4 {
      font-size: 1.25rem;
    }
    .row {
      display: flex;
      justify-content: center;

      .btn {
        width: 100%;
      }
      .wA-cont {
        margin-bottom: 15px;
      }
    }
  }
  footer {
    height: 150px;
  }
}
@media (min-width: 769px) and (max-width: 992px) {
  #pitch {
    .col-12 {
      display: block;
      line-height: 20px;

      .pitch-img {
        margin-bottom: 25px;
      }
      .pitch-content {
        h2 {
          font-size: 1.5rem;
        }
        p {
          font-size: 0.9rem;
        }
      }
    }
  }
}
@media (min-width: 769px) and (max-width: 1200px) {
  #testimonials {
    .t-slide-cont {
      width: 100% !important;
    }
  }
}
@media (min-width: 448px) {
  #home {
    .small-disp {
      .social-icons {
        font-size: 1.6rem;
      }
    }
  }
}
